@import url(./global.css);

.main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.main h1 {
    margin-right: 20px;
    padding-right: 20px;

    border-right: 1px solid #dee2e6!important;
}

/**
 * Navigate
 */
.back {
    position: fixed;
    left: 10px;
    top: 10px;

    display: flex;
    justify-content: flex-start;
    padding: 10px 0 0 4px;

    z-index: 20000;
}

.back .link--arrowed {
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.back .link--arrowed svg {
    transform: rotate(-180deg) !important;
}

.back .link--arrowed .arrow-icon {
    position: relative;
    top: -1px;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    vertical-align: middle
}

.back .link--arrowed .arrow-icon--circle {
    transition: stroke-dashoffset 0.3s ease;
    stroke-dasharray: 95;
    stroke-dashoffset: 95
}

.back .link--arrowed:hover {
    transform: translate3d(-4px,0,0);
}

.back .link--arrowed:hover .arrow-icon--circle {
    stroke-dashoffset: 0
}

@media (prefers-color-scheme: dark) {

    body {
        background-color: #181a1b;
    }

    .main {
        color: #e8e6e3;
    }

    .main h1 {
        border-right-color: rgb(56, 61, 63) !important;
    }

    .back svg g {
        stroke: #b2aca2 !important;
    }
}